import { Component, OnInit } from "@angular/core";
import { ITask } from "src/app/shared/interface/task.interface";
import { Task } from "src/app/shared/classes/task.model";

@Component({
  selector: "app-work-tasklist",
  templateUrl: "./work-tasklist.component.html",
  styleUrls: ["./work-tasklist.component.css"]
})
export class WorkTasklistComponent implements OnInit {
  arrTask: Array<ITask> = [
    { id: 1, name: "html", status: "Done", check: true },
    { id: 2, name: "css", status: "Done", check: true },
    { id: 3, name: "sass", status: "IN PROGRESS", check: false }
  ];
  taskStatus: string = "IN PROGRESS";
  taskName: string;
  checkTask: boolean;
  taskCount: number;
  renameTask: string;
  idTask: number;
  editTaskShow: boolean;

  constructor() {}

  ngOnInit() {}

  AddTask(): void {
    const newTask: ITask = new Task(
      1,
      this.taskName,
      this.taskStatus,
      this.checkTask
    );
    if (this.arrTask.length > 0) {
      newTask.id = this.arrTask.slice(-1)[0].id + 1;
    }
    this.arrTask.push(newTask);
    this.taskName = ''
    console.log(newTask);
  }

  checkBox(event: any, arrTask: any): void {
    console.log(event.target.checked);

    if (event.target.checked) {
      arrTask.status = "Done";
      arrTask.check = true;
      console.log(this.arrTask);
    } else {
      arrTask.status = "IN PROGRESS";
      arrTask.check = false;
      console.log(this.arrTask);
    }
  }

  deleteTask(task: ITask): void {
    const index = this.arrTask.findIndex(ts => ts.id === task.id);
    this.arrTask.splice(index, 1);
  }

  editTask(task: ITask): void {
    this.renameTask = task.name;
    this.idTask = task.id;
    this.editTaskShow = !this.editTaskShow;
  }

  editSave(): void {
    this.editTaskShow = !this.editTaskShow;
    this.arrTask.find(element =>
      element.id === this.idTask ? (element.name = this.renameTask) : undefined
    );
  }
}
