import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { HomeComponent } from "./pages/home/home.component";
import { WorkComponent } from "./work/work.component";
import { WorkCenzorComponent } from "./work/work-cenzor/work-cenzor.component";
import { WorkAddUsersComponent } from "./work/work-add-users/work-add-users.component";
import { WorkTasklistComponent } from "./work/work-tasklist/work-tasklist.component";

const routes: Routes = [
  { path: "", redirectTo: "/home", pathMatch: "full" },
  { path: "home", component: HomeComponent },
  {
    path: "work",
    component: WorkComponent,
    children: [
      { path: "cenzor", component: WorkCenzorComponent },
      { path: "add-users", component: WorkAddUsersComponent },
      { path: "tasklist", component: WorkTasklistComponent }
    ]
  },
  { path: "**", redirectTo: "/home" }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {}
