import { Component, OnInit } from "@angular/core";

@Component({
  selector: "app-work-cenzor",
  templateUrl: "./work-cenzor.component.html",
  styleUrls: ["./work-cenzor.component.css"]
})
export class WorkCenzorComponent implements OnInit {
  title = "cenzor";
  textSearch: string;
  arrBadWord: Array<string> = [];
  searchBad: Array<string> = [];
  textArea: string;
  allText: string = "";
  inputPlaceholderError: string = "text hear...";
  areaPlaceholderError: string = "write some text";
  isError: boolean;
  areaIsError: boolean;

  constructor() {}

  ngOnInit() {}

  addBadWords(): void {
    if (this.textSearch) {
      this.allText = "";
      this.arrBadWord.push(this.textSearch);
      this.allText += this.arrBadWord;
      this.textSearch = "";
      this.isError = false;
      this.inputPlaceholderError = "text hear...";
    } else {
      this.isError = true;
      this.inputPlaceholderError = "Input is empty";
    }
  }

  reset(): void {
    this.arrBadWord = [];
    this.allText = "";
    this.textArea = "";
  }

  censor() {
    if (this.textArea) {
      this.searchBad = this.textArea
        .replace(/[.,\/#?!$%\;;{}=\-_`~()]/g, "")
        .split(" ");
      this.arrBadWord.forEach(el => {
        this.searchBad.forEach((item, index) => {
          if (el === item) {
            item = item.replace(item, "*".repeat(item.length));
            this.searchBad.splice(index, 1, item);
          }
        });
      });
      this.textArea = this.searchBad.join(" ");
      this.areaIsError = false;
      this.areaPlaceholderError = "write some text";
    } else {
      this.areaPlaceholderError = "please write a text";
      this.areaIsError = true;
    }
  }
}
