import { Component, OnInit } from "@angular/core";
import { IUser } from "../../shared/interface/userlist.intarface";
import { User } from "../../shared/classes/userlist.model";

@Component({
  selector: "app-work-add-users",
  templateUrl: "./work-add-users.component.html",
  styleUrls: ["./work-add-users.component.css"]
})
export class WorkAddUsersComponent implements OnInit {
  arrUsers: Array<IUser> = [];
  userId: number;
  userLogin: string;
  userEmail: string;
  userPassword: string;
  editStatus: boolean;

  inputType: string = "password";
  mainButton: string = "add user";

  constructor() {}

  ngOnInit() {}

  addUser(): void {
    const newUser: IUser = new User(
      1,
      this.userLogin,
      this.userPassword,
      this.userEmail
    );
    if (this.arrUsers.length > 0) {
      newUser.id = this.arrUsers.slice(-1)[0].id + 1;
    }
    if (this.editStatus) {
      newUser.id = this.userId;
      const index = this.arrUsers.findIndex(us => us.id === newUser.id);
      this.arrUsers.splice(index, 1, newUser);
      this.editStatus = false;
      this.inputType = "password";
      this.mainButton = "add user";
    } else {
      this.arrUsers.push(newUser);
    }

    this.userLogin = "";
    this.userPassword = "";
    this.userEmail = "";
  }

  deleteUser(user: IUser): void {
    const index = this.arrUsers.findIndex(us => us.id === user.id);
    this.arrUsers.splice(index, 1);
  }

  editUser(user: IUser): void {
    this.userLogin = user.login;
    this.userPassword = user.password;
    this.userEmail = user.email;
    this.userId = user.id;
    this.editStatus = true;
    this.inputType = "text";

    this.mainButton = "save edit user";
  }
}
